.header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 10vh; /*change this for vh */
    background: #000;
}

.main {
    position: relative;
    background-image: linear-gradient(to bottom right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.95)), url(../../assets/images/gavel.jpg);
    margin-top: 10vh;
    height: 86vh;
    width: 100%;
    background-size: cover;
    background-attachment: fixed;
    background-position: top;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    color: #fff;
    background: #000;
    padding: .7rem;
    font-weight: 600;
    font-size: 1.2rem;
    height: 4vh; /*change this for vh */
}

